.pick-a-card {
  background-color: var(--color6);
  padding: 20px 0px 350px 0px;
  position: relative;
}

.pick-a-card h3 {
  text-align: center;
  color : white;
  top: 0;
  /* transform: translateX(50%); */
}

.pick-a-card-child {
  top: 0;
  bottom: 0;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  height: 210px;
  width: 150px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0, .45);
  border-radius: 0.6rem;
  cursor: pointer;
}

#pick-a-card-child1 {
  transform: rotate(26deg) translate(-68px, 26px);
}

#pick-a-card-child2 {
  transform: rotate(16deg) translate(-42px, 10px);
}

#pick-a-card-child3 {
  transform: rotate(9deg) translate(-17px, 1px);
}

#pick-a-card-child4 {
  transform: rotate(0deg) translate(12px, -6px);
}

#pick-a-card-child5 {
  transform: rotate(352deg) translate(40px, -7px);
}

#pick-a-card-child6 {
  transform: rotate(346deg) translate(63px, -3px);
}

#pick-a-card-child7 {
  transform: rotate(341deg) translate(86px, 1px);
}

#pick-a-card-child1:hover {
  transform: rotate(26deg) translate(-50px, 66px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child2:hover {
  transform: rotate(16deg) translate(-29px, 50px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child3:hover {
  transform: rotate(9deg) translate(-7px, 41px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child4:hover {
  transform: rotate(0deg) translate(16px, 34px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child5:hover {
  transform: rotate(352deg) translate(40px, 33px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child6:hover {
  transform: rotate(346deg) translate(63px, 37px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

#pick-a-card-child7:hover {
  transform: rotate(341deg) translate(86px, 41px);
  box-shadow: 0 .5rem 1rem #FFFFFF;
}

.child-portfolio-videos {
  vertical-align: middle;
  position: relative;
  height: 280px;
  width: 200px;
  margin : 10px;
  display:flex;
}

.card {
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.child-portfolio-videos img {
  border-radius: 0.6rem;
  height: 280px;
  width: 200px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0, .45);
  transition: transform 1s;
}



/* CSS */
.resetButton {
  position: absolute;
  top:50%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  border: 0 solid var(--color2);
  border-radius: 1.5rem;
  box-sizing: border-box;
  color: var(--color2);
  text-transform: uppercase;
  width: 140px;
  cursor: pointer;
  display: inline-block;
  font-weight: 600;
  line-height: 1;
  padding: 1rem 1.6rem;
  text-align: center;
  text-decoration: none #0d172a solid;
  text-decoration-thickness: auto;
  transition: all .1s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0px 1px 2px rgba(166, 175, 195, 0.25);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.resetButton p {
  margin:0;
  padding: 0;
}

.resetButton:hover {
  background-color: var(--color2);
  color: #fff;
}

@media (min-width: 768px) {
  .resetButton {
    font-size: 1.125rem;
    padding: 1rem 2rem;
  }
}

.child-portfolio-textblock {
  position: absolute;
  text-align: center;
  width: 90%;
  color: white;
  left:0;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 10px 0 10px;
  visibility: hidden;
  transition: opacity .2s, visibility .8s;
  opacity: 0;
}
.child-portfolio-textblock-open {
  position: absolute;
  text-align: center;
  width: 90%;
  color: white;
  left:0;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 10px 0 10px;
  transition: opacity .2s, visibility .8s;
}

.child-portfolio-videos-back {
  transform: rotateY(180deg);
  position: absolute;
  top:0;
  left:0;
}

.youtube {
  background-color: var(--color7);
  padding: 30px 0 30px 0;
}

#card-deck:empty {
  display:none;
}
.child-portfolio-videos-card {
  filter: brightness(69%);
}

.new-item:hover  .child-portfolio-textblock {
  opacity: 75%;
  visibility:visible;
}

.new-item:hover .child-portfolio-videos-back    {
  filter: brightness(60%);
  transform: rotateY(0);
}

.new-item:hover .child-portfolio-videos-front    {
  filter: brightness(60%);
  transform: rotateY(-180deg);
}


.arrowDown {
  font-size: 40px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 20px 0px;
  color: white;
  animation: bounce 3s ease infinite;
  cursor: pointer;
  transition: 250ms ease-in-out;
}

.arrowDown:hover {
  color: var(--color2);
}

/* The Modal (background) */
.modal-card {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.8); /* Black w/ opacity */
}

.modal-card-content {
  position:relative;
  display:flex;
  /* align-items: center; */
  justify-content: center;
  width: 100%;
  max-height: 1px;
  left: 50%;;
  transform: translateX(-50%);
  animation-name: animatetop;
  animation-duration: 0.4s;
}

.modal-card-content .child-portfolio-videos-back-modal {
  transform: rotateY(0deg);
  animation: transform 5s;
}


.card-visible .child-portfolio-videos-front-modal  {
  transform: rotateY(0deg);
}
.card-hidden .child-portfolio-videos-front-modal  {
  transform: rotateY(-180deg);
}

.modal-card-img {
  max-height: 80vh;
  margin: 15% auto;
}

.modal-card-icon {
  display: flex;
  width: 90%;
  justify-content: flex-end;
  position:absolute;
  top: 10%;
  padding: 1rem;
}

.modal-card-icon .icon {
  color: var(--color3);
  cursor: pointer;
}
.modal-card-icon:hover .icon {
  color: var(--color2);
}

.card-in-modal {
  margin-top: calc(50vh - 280px/2);
}

@keyframes bounce {
    80% { transform:translate(-50%,0%); }
    90% { transform:translate(-50%,-15%); }
    95% { transform:translate(-50%,0%); }
    97% { transform:translate(-50%,-7%); }
    98% { transform:translate(-50%,0%); }
    99% { transform:translate(-50%,-3%); }
    100% { transform:translate(-50%,0); }
}
